import React from "react";
import { BiRightArrowAlt } from "react-icons/bi";
import { FaRegCheckCircle } from "react-icons/fa";

const Criteria = (props: {
  trainee_criteria: {
    positions: { id: string; name: string }[];
    qualifications: { id: string; name: string }[];
    levels: { id: string; name: string }[];
    districts: { id: string; name: string }[];
    sectors: { id: string; name: string }[];
    subjects: {
      level_code: string;
      level_name: string;
      course_code: string;
      course_name: string;
    }[];
    strict: boolean;
  };
}) => {
  const ReturnCriteriaComponent = (props: {
    data: {
      id: string;
      name: string;
    }[];
  }) => {
    return (
      <div className="mt-2">
        {props.data.map((item, i) => (
          <div
            key={i + 1}
            className="bg-gray-100 mb-2 rounded-md px-2 py-2 flex flex-row items-center gap-2"
          >
            <div>
              <div className="bg-white rounded-full h-8 w-8 flex items-center justify-center text-primary-700">
                <FaRegCheckCircle className="text-xl" />
              </div>
            </div>
            <span>{item.name}</span>
          </div>
        ))}
      </div>
    );
  };
  return (
    <div>
      {props.trainee_criteria.positions.length > 0 && (
        <div className="mb-3">
          <div className="flex flex-row items-center gap-2">
            <div>
              <BiRightArrowAlt className="text-xl" />
            </div>
            <span className="font-bold">Required Position</span>
          </div>
          <ReturnCriteriaComponent data={props.trainee_criteria.positions} />
        </div>
      )}
      {props.trainee_criteria.qualifications.length > 0 && (
        <div className="mb-3">
          <div className="flex flex-row items-center gap-2">
            <div>
              <BiRightArrowAlt className="text-xl" />
            </div>
            <span className="font-bold">Required Qualification</span>
          </div>
          <ReturnCriteriaComponent
            data={props.trainee_criteria.qualifications}
          />
        </div>
      )}
      {props.trainee_criteria.subjects.length > 0 && (
        <div className="mb-3">
          <div className="flex flex-row items-center gap-2">
            <div>
              <BiRightArrowAlt className="text-xl" />
            </div>
            <span className="font-bold">Required Subjects</span>
          </div>
          <ReturnCriteriaComponent
            data={props.trainee_criteria.subjects.map((item) => ({
              id: `${item.course_code}`,
              name: `${item.course_name} - ${item.level_name}`,
            }))}
          />
        </div>
      )}
      {props.trainee_criteria.districts.length > 0 && (
        <div className="mb-3">
          <div className="flex flex-row items-center gap-2">
            <div>
              <BiRightArrowAlt className="text-xl" />
            </div>
            <span className="font-bold">Required Districts</span>
          </div>
          <ReturnCriteriaComponent data={props.trainee_criteria.districts} />
        </div>
      )}
      {props.trainee_criteria.levels.length > 0 && (
        <div className="mb-3">
          <div className="flex flex-row items-center gap-2">
            <div>
              <BiRightArrowAlt className="text-xl" />
            </div>
            <span className="font-bold">Required teaching level</span>
          </div>
          <ReturnCriteriaComponent data={props.trainee_criteria.levels} />
        </div>
      )}
      {props.trainee_criteria.sectors.length > 0 && (
        <div className="mb-3">
          <div className="flex flex-row items-center gap-2">
            <div>
              <BiRightArrowAlt className="text-xl" />
            </div>
            <span className="font-bold">Required sectors</span>
          </div>
          <ReturnCriteriaComponent data={props.trainee_criteria.sectors} />
        </div>
      )}
    </div>
  );
};

export default Criteria;
